@import url(https://fonts.googleapis.com/css?family=Bangers|Creepster|Ewert|Fascinate|Faster+One|Metal+Mania|Monoton|Oleo+Script+Swash+Caps|Pacifico|Patua+One|Permanent+Marker|Piedra|Press+Start+2P|Ranchers|Trade+Winds&display=swap);
