a, li, p, button, span, b, input, textarea, option, select {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
}

a {
  color: white;
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
  li {
    padding-left: 1rem;
  }
}

#loader {
  position: absolute;
  height: 100vh;
  width: 100vw;
  opacity: 1;
  background: #00838F;
  z-index: 100;
  display: flex;
  justify-content: center;
  flex-direction: column;
  @keyframes dots {
    0%   {content: ""}
    25%   {content: "."}
    50%   {content: ".."}
    75%   {content: "..."}
  }
  h1 {
    text-align: center;
    color: white;
    &::after{
      content: '';
      animation: dots 2s infinite;
    }
  }
}

#search {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  li {
    padding: .5rem 1rem;
  }
}

ol {
  padding-left: 2rem;
}

input, textarea {
  box-sizing: border-box; 
  background: white;
  margin: .5rem 0;
  padding: 1rem;
  border: none;
  color: black;
  font-size: 1rem;
}

form {
  button, select, input[type=number] {
    max-width: 200px;
  }
}

#loginForm, #registrationForm, #hero, #recipes, #recipe, #user, #addRecipe, #myRecipes {
  z-index: 5;
  overflow: hidden;
  position: absolute;
  height: 100vh;
  width: 100%;
}

button {
  max-width: 200px;
  position: relative;
  border: none;
  font-weight: bold;
  padding: .5rem 1.5rem;
  cursor: pointer;
  outline: none;
  color: black;
  margin: 0;
  background: white;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: 1rem;
  transition: box-shadow .2s, top .3s, left .3s, color .2s;
  &:hover {
    top: -2px;
    left: -2px;
    color: white;
    background: #00838F;
    box-shadow: 3px 3px 3px darken(#00838F, 20%)
  }
}

#hamburger {
  display: none;
  top: 0;
  left: 0;
  color: black;
  background: white;
  &.active {
    color: white;
    background: #00838F;
    box-shadow: 3px 3px 3px darken(#00838F, 20%)
  }
}

select {
  max-width: 200px;
  width: 100%;
  height: 35px;
  top: 1px;
  position: relative;
  border: none;
  font-weight: bold;
  padding: .5rem 1.5rem;
  cursor: pointer;
  outline: none;
  color: black;
}

#root {
  max-height: 100vh;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  position: relative;
  background: url(bg.jpg);
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
  z-index: 0;
  .bg {
    z-index: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: radial-gradient(
      rgba(0,0,0,0.2) 10%,
      rgba(0,0,0,0.4) 30%,
      rgba(0,0,0,0.6) 50%,
      rgba(0,0,0,0.8) 70%,
      rgba(0,0,0,1) 95%)
  }
}

.nav {
  position: absolute;
  z-index: 10;
  top: 0px;
  width: 100%;
  background: #00838F;
  color: white;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  a {
    cursor: pointer;
    padding: 1rem;
    position: relative;
    &:hover {
      color: black;
      background: white;
    }
  }
  p {
    padding-right: 1rem;
    font-family: 'Oleo Script Swash Caps', cursive;
    font-size: 1.5rem;
    margin: auto;
  }
  div, .navWrap {
    display: flex;
  }
}

#hero {
  display: flex;
  #hero--wrapper {
    color: white;
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    h1 {
      font-family: 'Creepster', cursive;
      margin: 5rem 0;
      font-size: 12rem;
      overflow-wrap: break-word;
    }
    p {
      padding: 2rem 0;
      width: 100%;
    }
    @mixin hero--extra {
      position: absolute;
      text-align: center;
      width: 100%;
    }
    .hero--nav {
      @include hero--extra;
      margin-top: 4rem;
      top: 0;
    }
    .hero--buttons {
      @include hero--extra;
      margin-bottom: 4rem;      
      bottom: 0;
    }
  }
}

#loginForm, #registrationForm {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  form {
    position: relative;
    display: flex;
    flex-direction: column;
    button {
      margin-top: 1rem;
    }
  }
}

@mixin recipes {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  .wrapper--wrapper {
    overflow: hidden;
    width: 60%;
  }
  .wrapper {
    width: 100%;
    max-height: 90vh;
    overflow-y: auto;
    overflow-x: hidden;
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    button {
      margin: 0;
    }
    .recipe__Header {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
  }
}

#recipes {
  @include recipes;
}

#myRecipes {
  @include recipes;
}

#recipe {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  div {
    color: white;
    padding: 1rem;
    position: relative;
    width: 60%;
  }
}

#user {
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: white;
  .user--wrapper {
    width: 60%;
    margin: auto;
  }
  form {
    flex-direction: column;
    display: flex;
    button {
      margin-top: 1rem;
    }
  }
}

#addRecipe {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;

  .formWrap {
    overflow: hidden;
    width: 60%;
    max-height: 90vh;
    .form {
      max-height: 80vh;
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      padding-right: 20px;

      form {
        display: flex;
        flex-direction: column;
        
        select {
          margin-bottom: 1rem;
        }

        textarea {
          resize: vertical;
          &::placeholder {
            font-family: 'Montserrat', sans-serif;
          }
        }

        button {
          margin-top: 1rem;
        }
      }
    }
  }
}

// Responsive
// Responsive
@media screen and (min-width: 1000px) {
  .nav {
    display: flex;
  }
}

@media screen and (max-width: 1000px) {
  button#hamburger {
    position: absolute;
    z-index: 100;
    top: 0;
    max-width: 100%;
    margin: 0;
    display: block;
  }

  .nav, .hero--nav, #recipe div {
    padding-top: 2.5rem;
  }

  .nav {
    display: none;
    flex-direction: row;
    div, .navWrap {
      width: 50%;
      display: flex;
      flex-direction: column;
    }
    p {
      margin: 0;
      padding-left: 1rem;
    }
  }
  #hero {
    #hero--wrapper {
      .noUser {
        max-width: 100%;
      }
      .hero--nav {
        margin-top: 0;
        button {
          margin: 0;
        }
      }
      .hero--buttons {
        margin-bottom: 0;
      }
      h1 {
      font-size: 8rem !important;
      margin: 0 !important;
      }
      p {
        font-size: 1rem !important;
        margin: 0 !important;
        padding: 0;
      }
      button {
        max-width: 50%;
      }
      select {
        max-width: 100%;
      }
    }
  }
  
  #user, #recipes, #recipe, #myRecipes {
    overflow: auto;
    div {
      overflow: auto;
      width: 60%;
      max-height: 80vh;
    }
  }

  #recipes, #myRecipes {
    div {
      overflow: initial;
    }
  }

  #recipe {
    button {
      max-width: 33%;
    }
  }
}

@media screen and (max-width: 700px) {
  #hero #hero--wrapper {
    h1 {
    font-size: 5rem !important;
    margin: 0 !important;
    }
    p {
      font-size: 1.2rem !important;
    }
  }
}

@media screen and (max-width: 600px) {
  #search {
   li {
     width: 100%;
   } 
  }
  #recipe {
    button {
      max-width: 100%;
    }
  }

  #user, #recipes, #recipe, #myRecipes, #addRecipe {
    div {
      width: 90% !important;
    }
  }
}